/*
@styleguide

@title Button

Use the button classes on and `<a>`, `<button>`, `<input>` elements.

<button class="button button--large button--red">Red Button</button>

    <button class="button button--large button--red">Red Button</button>

<button class="button button--large button--blue">Blue Button</button>

    <button class="button button--large button--blue">Blue Button</button>
*/
.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  cursor: pointer; }

.button--large {
  width: 140px;
  height: 40px;
  font-size: 14px; }

.button--red {
  color: #fff;
  background-color: red; }

.button--blue {
  color: #fff;
  background-color: blue; }

/*
@styleguide

@title Tags

Use the button classes on and `<a>`, `<button>`, `<input>` elements.

<button class="tag tag--large tag--red">Red Tag</button>

    <button class="tag tag--large tag--red">Red Tag</button>

<button class="tag tag--large tag--blue">Blue Tag</button>

    <button class="tag tag--large tag--blue">Blue Tag</button>
*/
.tag {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100px;
  cursor: pointer;
  font-size: 12px;
  padding: 1rem; }

.tag--red {
  color: #fff;
  background-color: red; }

.tag--blue {
  color: #fff;
  background-color: blue; }
